@font-face {
    font-family: "Lato Regular";
    src: url(../fonts/Lato-Regular.ttf);
}

.header {
    width: 100%;
    background-color: #fff;
}

.header__content {
    width: 1170px;
    padding: 22px 0 24px 0;
    margin: 0 auto;
}
.logo {
    display: inline-block;
    &__icon {
        width: 50px;
        height: 47px;
    }
}

.nav {
    font-family: "Lato Regular";
    display: inline-block;
    margin-left: 310px;
    &__list {
        display: inline-block;
        list-style-type: none;
        font-size: 0;
    }
    &__item {
        display: inline-block;
    }
    &__link {
        display: inline-block;
        text-decoration: none;
        font-size: 13px;
        font-weight: bold;
        text-transform: uppercase;
        color: #8e8e8e;
        padding: 12px 20px;
    }
    &__link:hover {
        color: #fff;
        background-color: #ffbb42; 
    }
}

